body {
 background-color: darkgreen;
 font-family: Georgia, serif;
}

h1 {
  color: #FFFFFF;
  text-align: center;
}

p {
  font-size: 12px;
  margin-inline-start: 15px;
  position: relative;
  display: inline;
  
  
}

.deckbox {
    background: #FFFFFF;
    border: 3px solid #999;
    border-radius: 10px;
    font-size: 20px;
    line-height: 34px;
    height: 230px;
    margin-left: 25px;
    margin-right: 50px;
    margin-top: 0px;
    margin-bottom: 25px;
    padding: 0;
    text-align: center;
    width: 150px;
    display: inline-block;
    position: relative;
    
  }

  .collectbox {
    background: #FFFFFF;
    border: 3px solid #999;
    font-size: 24px;
    line-height: 34px;
    height: 200px;
    margin-right: 10px;
    margin-top: 10px;
    padding: 0;
    text-align: center;
    width: 150px;
    float: right;
  }

  .tableau {
    background: darkgreen;
    border: none/*3px solid #999*/;
    font-size: 18px;
    line-height: 34px;
    height: 235px;
    margin-right: 25px;
    margin-left: 25px;
    margin-top: 20px;
    padding: 0;
    text-align: center;
    width: 150px;
    vertical-align: top
  }

  .newgame {
    background: #FFFFFF;
    border: 3px solid #999;
    border-radius: 10px;
    font-size: 24px;
    line-height: 34px;
    height: 50px;
    margin-right: 50px;
    margin-left: 45px;
    margin-top: 20px;
    padding: 0;
    text-align: center;
    width: 125px;
    position: left;
  }
  .scorebox {
    background:#B0C4DE;
    border: 3px solid #999;
    font-size: 12px;
    line-height: 34px;
    height: 125px;
    margin-right: 50px;
    margin-top: 60px;
    padding: 0;
    text-align: left;
    width: 175px;
    position: right;
    float: right;
  }

  ul {
    list-style-type: none;
    justify-content: left;
    padding-inline-start: 15px;
    margin-block-end: 0em;
  }

  .card {
    width: 150;
    height: 250;
    margin-right: 25px;
    margin-left: 25px;
    margin-top: 0px;
  }

  .cardback{
      width: 150;
      height: 250;
      margin-right: 50px;
      margin-left:25px;
      margin-top: 0px;
      position: relative;
  }